<template>
	<view class="commodity" :style="'flex-wrap:'+wrap+';'">
		<!-- 单个商品组件 -->
		<view class="commodity-item" :style="'width:' +itemW+ ';'" v-for="item in datalist" :key="item.id" @tap="goDetails(item.id)">
			<image class="commodity-img" :style="'height:'+bigH+';'" :src="item.imgUrl" mode=""></image>
			
			<view class="commodity-content">
				<text class="commodity-name" :style="'font-size:'+nameSize+ ';'">
					{{item.name}}
				</text>
				<view>
					<text class="pprice">{{item.pprice}}</text>
					<text class="oprice">{{item.oprice}}</text>
				</view>
				<text class="discount">{{item.discount}}折</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		props:{
			datalist:Array,
			itemW:{
				type:String,
				default:"375rpx"
			},
			bigH:{
				type:String,
				default:"375rpx"
			},
			wrap:{
				type:String,
				default:"wrap"
			},
			nameSize:{
				type:String,
				default:"26rpx"
			}
		},
		methods:{
			goDetails(id){
				uni.navigateTo({
					url:`/pages/details/details?id=${id}`
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.commodity{
		display: flex;
		// flex-wrap: wrap;
		.commodity-item{
			// width: 375rpx; 已经传递
			padding-bottom: 20rpx;
			.commodity-img{
				width: 100%;
				// height: 375rpx; 已经传递
			}
			.commodity-content{
				text-align: center;
				.commodity-name{
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					color: #333;
					word-break: break-all;
					padding: 6rpx 20rpx;
				}
				view{
					.oprice{
						text-decoration: line-through;
						font-size: 24rpx;
						color: #999;
					}
				}
				.discount{
					border-radius: 4rpx;
					border: 1px solid #ff3333;
					padding: 2rpx 10rpx;
					font-size: 20rpx;
					color: #ff3333;
				}
			}
		}
	}
</style>